const Header = (props) => (
    <div className="navBar">
        <ul>
            {console.log(props.data)}
            
            {props.data.forEach(item => (
                <li>
                    <a href={item.href} className="aNavbar">{item.label}</a>
                </li>
            ))}
        </ul>
    </div>
)

export default Header